import React,{useEffect, useRef} from "react";
import { ReactComponent as UserImage } from "../../assets/userImage.svg";
import gsap from 'gsap'; 

const User = () => {
  const imageWrapper = useRef(null);
  
  useEffect(() => {
      const [elements] = imageWrapper.current.children;

      const circles = elements.querySelectorAll('.cls-2__circles');
      const stars = elements.querySelectorAll('.cls-2__stars');
      const cloudRight = elements.querySelector('.cls-1__clouds--cloudOne')
      const cloudLeft = elements.querySelector('.cls-1__clouds--cloudTwo')

    gsap.set([...circles, ...stars, cloudRight, cloudLeft], {autoAlpha: 0})

      const tl = gsap.timeline({defaults: {ease: 'power3.inOut'}});

      tl.fromTo(cloudRight, {x : '-=100'}, {duration: 1.5, x : '+=100', autoAlpha: 1})
        .fromTo(cloudLeft, {x : '+=100'}, {duration: 1.5, x : '-=100', autoAlpha: 1}, '-=.80')
        .to(stars, {duration: 2, autoAlpha: 1, stagger: .2}, '-=1')
        .to(circles, {duration: 2, autoAlpha: 1, stagger: .2}, '-=3')
  }, []);
    return (
    <div className="User">
      <div ref={imageWrapper} className="User__imageWrapper">
        <UserImage />
      </div>
    </div>
  );
};

export default User;
